<template>
  <div class="homeBox">
    <el-container style="height: 100%;">
      <el-header style=" border-bottom: 1px solid #eee;display: flex; align-items: center; height: 50px;">
        <el-button class="el-buttonOfHeader" style="width: 205px;height: 50px;text-align:center;border-right: none;margin-left: -21px;margin-top: 1px;">
            <img class = "image" src="https://ts1.cn.mm.bing.net/th?id=OIP-C.l7DqF8uxZM2mgyGWBDQIHAHaHa&w=250&h=250&c=8&rs=1&qlt=90&o=6&dpr=1.3&pid=3.1&rm=2">
        </el-button>
        <div style="display:flex; align-items: center;">
          <!--  -->
          <el-breadcrumb separator-class="el-icon-arrow-right" style="margin-left: 20px;margin-top:10px;margin-bottom:10px;">
            <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
            <el-breadcrumb-item :to="{ path: '/emp' }"></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
          </el-breadcrumb>
         
        </div>
        <!-- <el-button class="el-buttonOfHeader" style="margin-left: auto; margin-right: 20px;"></el-button> -->
      </el-header>
      <el-container>
        <el-aside class="aside" width="204px">
            <el-menu 
                :default-active="$route.path" 
                class="el-menu-vertical-demo" 
                background-color="#3a4a6e "
                @select="handleSelect"
                text-color="#fff"
                active-text-color="#ffd04b"
                router
                style="border: 0px solid #3a4a6e">
                <el-menu-item index="/" >
                    <i class="el-icon-location"></i>
                    <span slot="title"></span>
                </el-menu-item>
                <el-submenu index="$route.path" >
                    <template slot="title">
                        <i class="el-icon-message"></i>
                        <span></span>
                    </template>
                    <el-menu-item index="/dept">
                    <i class="el-icon-edit"></i>
                    
                    </el-menu-item>
                    <el-menu-item index="/emp">
                    <i class="el-icon-edit"></i>
                    
                    </el-menu-item>
                </el-submenu>
            </el-menu>
        </el-aside>
        <el-main style="padding:20px">
          <!--  -->
          <el-form
            :inline="true"
            :model="formInline"
            class="demo-form-inline"
            ref="formInline"
            size="medium"
          >
            <el-form-item label="" prop="name">
              <el-input v-model="formInline.name" placeholder="" ></el-input>
            </el-form-item>
            <el-form-item label="" prop="gender">
              <el-select v-model="formInline.gender" placeholder="">
                <el-option label="" value="1"></el-option>
                <el-option label="" value="2"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="" prop="entrydate">
              <el-date-picker
                v-model="formInline.entrydate"
                type="daterange"
                range-separator=""
                start-placeholder=""
                end-placeholder=""
                value-format="yyyy-MM-dd"
              ></el-date-picker>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="select"></el-button>
            </el-form-item>
            <el-form-item>
              <el-button @click="resetForm('formInline')"></el-button>
            </el-form-item>
          </el-form>
          <!--  -->
           <div style="display: flex; justify-content:flex-start">
          <el-button
            type="danger"
            icon="el-icon-remove"
            size="small"
            @click="alldelete"
            :disabled="multipleSelection.length === 0"> </el-button>
       
           <el-button
            type="primary"
            size="mini"
            icon="el-icon-circle-plus"
            @click="addDialogVisible = true"></el-button>
          </div>
          <br>
          <!--  -->
          <el-table :data="tableData" border 
          :header-cell-style="{ background: '#F2F6FC', color: '#606266' }"
          ref="multipleTable"
          size="medium"
          @selection-change="handleSelectionChange">
          
            <el-table-column type="selection" width="60" align="center"></el-table-column>
            <el-table-column prop="name" label="" width="150" align="center"></el-table-column>
            <el-table-column prop="image" label="" width="150" align="center">
              <template slot-scope="scope">
                <img :src="scope.row.image" width="75px" height="60px" />
              </template>
            </el-table-column>
            <el-table-column prop="gender" label="" width="150" align="center">
              <template slot-scope="scope">
                {{ scope.row.gender == 1 ? "" : "" }}
              </template>
            </el-table-column>
            <el-table-column prop="job" label="" width="150" align="center">
             <template slot-scope="scope">
                  <span v-if="scope.row.job == 1"></span>
                  <span v-else-if="scope.row.job == 2"></span>
                  <span v-else-if="scope.row.job == 3"></span>
                  <span v-else-if="scope.row.job == 4">ADC</span>
                  <span v-else></span>
              </template>
            </el-table-column >
            <el-table-column prop="entrydate" label="" width="200" align="center"></el-table-column>
            <el-table-column prop="updateTime" label="" width="200" align="center">
               <template slot-scope="scope">
                {{ new Date(scope.row.updateTime).toLocaleString().replace(/\//g,'-') }}
              </template>
            </el-table-column>
            <el-table-column label="" align="center">
               <template slot-scope="scope">
                <el-button
                  type="primary"
                  size="small"
                  @click="editDialogVisible = true; beforeHandleEdit(scope.row)"></el-button>
                <el-button
                  size="small"
                  type="danger"
                  @click="handleDelete( scope.row.id)"></el-button>
              </template>
            </el-table-column>
          </el-table>

          <!-- dialog -->
         <el-dialog class="dialogClass" title="" :visible.sync="addDialogVisible"  
          :style="{ width: '55%', height: '98%', padding: '10px', margin: '-3% auto'}" center :before-close="beforeCloseDIalog">
            <el-form :model="addDialogForm" :rules="addDialogFormRules" ref="addDialogForm" 
              label-position="right" label-width="80px" size="mini">
              <el-form-item label="" prop="username" >
                <el-input v-model="addDialogForm.username" autocomplete="off" placeholder=""></el-input>
              </el-form-item>
              <el-form-item label="" prop="name" >
                <el-input v-model="addDialogForm.name" autocomplete="off" placeholder=""></el-input>
              </el-form-item>
              <el-form-item label=""  prop="gender" >
                <el-select v-model="addDialogForm.gender" placeholder="">
                  <el-option label="" value="1"></el-option>
                  <el-option label="" value="2"></el-option>
                </el-select>
              </el-form-item>
              <el-form-item label="" prop="image">
                <el-upload
                v-model="addDialogForm.image" placeholder=""
                  class="avatar-uploader"
                  action="https://jsonplaceholder.typicode.com/posts/"
                  :show-file-list="false"
                  :before-upload="beforeAvatarUpload">
                  <img v-if="addDialogForm.image" :src="addDialogForm.image" class="avatar">
                  <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                </el-upload>
              </el-form-item>
              <el-form-item label=""  prop="job" >
                <el-select v-model="addDialogForm.job" placeholder="">
                  <el-option label="" value="1"></el-option>
                  <el-option label="" value="2"></el-option>
                  <el-option label="" value="3"></el-option>
                  <el-option label="ADC"  value="4"></el-option>
                  <el-option label="" value="5"></el-option>
                </el-select>
              </el-form-item>
              <el-form-item label="" prop="deptId" >
                <el-select v-model="addDialogForm.deptId" placeholder="">
                  <el-option label="" value="1"></el-option>
                  <el-option label="" value="2"></el-option>
                  <el-option label="" value="3"></el-option>
                  <el-option label="" value="4"></el-option>
                  <el-option label="" value="5"></el-option>
                  <el-option label="" value="6"></el-option>
                </el-select>
              </el-form-item>
              <el-form-item label="" prop="entrydate" >
                <el-date-picker v-model="addDialogForm.entrydate" type="date" placeholder=""></el-date-picker>
              </el-form-item>           
              <el-form-item>
                <el-button @click="resetDialogForm('addDialogForm')"> </el-button>
                <el-button type="primary" @click="handleAdd"> </el-button>
              </el-form-item>
            </el-form>
          </el-dialog>
          <!-- dialog -->
         <el-dialog class="dialogClass" title="" :visible.sync="editDialogVisible"  
          :style="{ width: '55%', height: '98%', padding: '10px', margin: '-3% auto'}" center :before-close="beforeCloseDIalog">
            <el-form :model="editDialogForm" :rules="addDialogFormRules" ref="editDialogForm" 
              label-position="right" label-width="80px" size="mini">
              <el-form-item label="" prop="username" >
                <el-input v-model="editDialogForm.username" autocomplete="off" placeholder=""></el-input>
              </el-form-item>
              <el-form-item label="" prop="name" >
                <el-input v-model="editDialogForm.name" autocomplete="off" placeholder=""></el-input>
              </el-form-item>
              <el-form-item label=""  prop="gender" >
                <el-select v-model="editDialogForm.gender" placeholder="">
                  <el-option label="" :value="1"></el-option>
                  <el-option label="" :value="2"></el-option>
                </el-select>
              </el-form-item>
              <el-form-item label="" prop="image">
                <el-upload
                v-model="editDialogForm.image" placeholder=""
                  class="avatar-uploader"
                  action="https://jsonplaceholder.typicode.com/posts/"
                  :show-file-list="false"
                  :before-upload="beforeAvatarUpload">
                  <img v-if="editDialogForm.image" :src="editDialogForm.image" class="avatar">
                  <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                </el-upload>
              </el-form-item>
              <el-form-item label="" prop="job">

                <el-select v-model="editDialogForm.job" placeholder="">
                  <el-option label="" :value="1"></el-option>
                  <el-option label="" :value="2"></el-option>
                  <el-option label="" :value="3"></el-option>
                  <el-option label="ADC"  :value="4"></el-option>
                  <el-option label="" :value="5"></el-option>
                </el-select>
              </el-form-item>
              <el-form-item label="" prop="deptId" >
                <el-select v-model="editDialogForm.deptId" placeholder="">
                  <el-option label="" :value="1"></el-option>
                  <el-option label="" :value="2"></el-option>
                  <el-option label="" :value="3"></el-option>
                  <el-option label="" :value="4"></el-option>
                  <el-option label="" :value="5"></el-option>
                  <el-option label="" :value="6"></el-option>
                </el-select>
                <!-- <el-select v-model="editDialogForm.deptId" placeholder="">
                <el-option
                  v-for="dept in depts"
                  :key="dept.id"
                  :label="dept.name"
                  :value="dept.id"
                ></el-option>
              </el-select> -->
              </el-form-item>
              <el-form-item label="" prop="entrydate" >
                <el-date-picker v-model="editDialogForm.entrydate" type="date" placeholder=""></el-date-picker>
              </el-form-item>           
              <el-form-item>
                <el-button @click="editDialogVisible = false"> </el-button>
                <el-button type="primary" @click="handleEdit"> </el-button>
              </el-form-item>
            </el-form>
          </el-dialog>
          <br />
          <!--  -->
          <el-pagination
            background
            layout="total,sizes,prev, pager, next,jumper"
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :total="total"
            :page-sizes="[5, 10, 20, 50, 100]"
            :page-size="pageSize"
            :current-page="page"
          ></el-pagination>
        </el-main>
      </el-container>
    </el-container>
  </div>
</template>

<script>
import axios from "axios";
import OSS from 'ali-oss/dist/aliyun-oss-sdk.min.js';
const client = new OSS({
  region: 'oss-cn-beijing',
  accessKeyId: 'LTAI5t7JXcd2N4eNBtLrmpak',
  accessKeySecret: 'BPdEnrpOdd40gjwgvGuJvUfWCsGuSD',
  bucket: 'java-web-study',
});

export default {
  data() {
    return {
      activeIndex : '/',
      tableData: [],
      formInline: {
        name: "",
        gender: "",
        entrydate: "",
        
      },
       jobOptions: [
        { value: '1', label: '' },
        { value: '2', label: '' },
        { value: '3', label: '' },
        { value: '4', label: 'ADC' },
        { value: '5', label: '' },
     ],
      depts: [],
      addDialogVisible: false,
      editDialogVisible: false,
      addDialogFormRules: {
        username: [{ required: true, message: "", trigger: "blur" }],
        name: [{ required: true, message: "", trigger: "blur" }],
        gender: [{ required: true, message: "", trigger: "change" }],
        job: [{ required: true, message: "", trigger: "blur" }],
        deptId: [{ required: true, message: "", trigger: "blur" }],
        entrydate: [{ required: true, message: "", trigger: "change" }]
      },
      addDialogForm:{
        username: "",
        name: "",
        gender: "",
        image: "",
        job: "",
        deptId: "",
        entrydate: "",
      },
      editDialogForm:{
        id:"",
        username: "",
        name: "",
        gender: "",
        image: "",
        job: "",
        deptId: "",
        entrydate: "",
      },
      begin:"",
      end:"",
      total: 10,
      pageSize: 5,
      page: 1,
      multipleSelection: [], //
    };
  },
  watch: {
        $route () {
            this.handleSelect(this.activeIndex);
        }
     },
  methods: {
    //
     handleSelect (index) {
            this.activeIndex = index;
        },
    //
    select() {
      this.fetchData();
    },
    //
    resetForm(formName) {
        this.$refs[formName].resetFields();
        this.fetchData();
      },
    //
    handleSizeChange(val) {
      this.pageSize = val;
      this.fetchData();
    },
    //
    handleCurrentChange(val) {
      this.page = val;
      this.fetchData();
    },
    //dialogForm
    resetDialogForm(formName) {
        this.$refs[formName].resetFields();
      },
    //dialog
    beforeCloseDialog(done) {
      
      this.$refs.addDialogForm.validate(valid => {
        if (valid) {
          done();
        } else {
          return false;
        }
      });
    },
    //
    beforeAvatarUpload(file) {
      const isJPG = file.type === "image/jpeg";
      const isPNG = file.type === "image/png";
      const isLt5M = file.size / 1024 / 1024 < 5;

      if (!isJPG && !isPNG) {
        this.$message.error(" JPG/PNG !");
        return false;
      }
      if (!isLt5M) {
        this.$message.error(" 5MB!");
        return false;
      }

      const reader = new FileReader();
      reader.readAsArrayBuffer(file);
      reader.onload = event => {
        const imgData = event.target.result;
        const filename = file.name;
        const blob = new Blob([imgData]);
        client.put(filename, blob).then(res => {
          console.log("", res.url);
          this.addDialogForm.image = res.url;
          this.editDialogForm.image = res.url;
        });
};

      return true;
    },
    //
    async handleAdd(){
      const confirmR = await this.$confirm(
        "?",
        "",
        {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning",
          center: true,
        }
      ).catch((err) => err);
      if (confirmR == "confirm"){
        //
        axios.
          post("/api/emps/",{
               username: this.addDialogForm.username,
              name: this.addDialogForm.name,
              gender: this.addDialogForm.gender,
              image: this.addDialogForm.image,
              job: this.addDialogForm.job,
              deptId: this.addDialogForm.deptId,
              entrydate: this.addDialogForm.entrydate
            },
            {
             headers: {
            'token': localStorage.getItem('token')
          }
          })
          .then(result => {
          console.log(result.data.code);
          if(result.data.code == 0){
            return this.$message.error("~");
          }
          this.$message.success("");
          this.addDialogVisible = false;
          this.addDialogForm={//
            username: "",
                name: "",
                gender: "",
                image: "",
                job: "",
                deptId: "",
                entrydate: ""

          };
          this.fetchData();
        }).catch(error => {
          console.log(error);
          this.$message.error("");
        });
      }else{
        return this.$message.info("");
      }  
    },
    //
    async handleEdit(){
      const confirmR = await this.$confirm(
        "?",
        "",
        {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning",
          center: true,
        }
      ).catch((err) => err);
      if (confirmR == "confirm"){
        //
        axios.
          put("/api/emps/",{
               id: this.editDialogForm.id,
              username: this.editDialogForm.username,
              name: this.editDialogForm.name,
              gender: this.editDialogForm.gender,
              image: this.editDialogForm.image,
              job: this.editDialogForm.job,
              deptId: this.editDialogForm.deptId,
              entrydate: this.editDialogForm.entrydate
            },
            {
          headers: {
            'token': localStorage.getItem('token')
          }
          })
          .then(result => {
          console.log(result.data.code);
          if(result.data.code == 0){
            return this.$message.error("");
          }
          this.$message.success("");
          this.editDialogVisible = false;
          this.editDialogForm={//
            username: "",
                name: "",
                gender: "",
                image: "",
                job: "",
                dept: "",
                entrydate: ""

          };
          this.fetchData();
        }).catch(error => {
          console.log(error);
          this.$message.error("");
        });
      }else{
        return this.$message.info("");
      }
    },
    //,
    beforeHandleEdit(rows){
     // this.editDialogForm = rows;
     //json
     this.editDialogForm = JSON.parse(JSON.stringify(rows));
     this.editDialogForm.id = rows.id;
    },
    //
    handleSelectionChange(val) {
      // console.log(val);
      this.multipleSelection = [];
      for (let selectedItem of val) {
        this.multipleSelection.push(selectedItem.id);
      }
      // console.log(this.multipleSelection);
    },
    // 
    async handleDelete(id) {
      // console.log("id:", id);
      const confirmR = await this.$confirm(
        ", ?",
        "",
        {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning",
          center: true,
        }
      ).catch((err) => err);
      if (confirmR == "confirm") {
        // 
        axios.
          delete("/api/emps/"+id,{
            headers: {
            'token': localStorage.getItem('token')
          }
          })
          .then(result => {
          console.log(result.data.code);
          if(result.data.code == 0){
            return this.$message.error("");
          }
          this.$message.success("");
          this.fetchData();
        })
      }  else {
        return this.$message.info("");
      }
    },
    //
    async alldelete() {
      // console.log(this.multipleSelection);
      const confirmR = await this.$confirm(
        ", ?",
        "",
        {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning",
          center: true,
        }
      ).catch((err) => err);
      if (confirmR == "confirm") {
        // 
        axios.
          delete("/api/emps/"+this.multipleSelection,{
             headers: {
            'token': localStorage.getItem('token')
          }
          })
          .then(result => {
          console.log(result.data.code);
          if(result.data.code == 0){
            return this.$message.error("");
          }
          this.$message.success("");
          this.fetchData();
        })
      }  else {
        return this.$message.info("");
      }
    },
    fetchData() {
      axios
        .get("/api/emps", {
          params: {
            name: this.formInline.name,
            gender: this.formInline.gender,
            begin: this.formInline.entrydate[0],
            end: this.formInline.entrydate[1],
            pageSize: this.pageSize,
            page: this.page,
          },
          headers:{
          'token': localStorage.getItem('token')
          },
        })
         .then(result => {
          console.log(result.data.data);
          console.log(this.formInline.entrydate[0]);
          console.log(this.formInline.entrydate[1]);
          this.tableData = result.data.data.rows;
          this.total = result.data.data.total;
        })
    },
  },
  mounted() {
    //
    this.fetchData();
    
  },
  created(){
      // token
      if(!localStorage.getItem('token')){
        this.$router.push('/login');
      }
    },
  // created(){
  //   this.$bus.$on('updateDepts', (depts) => {
  //   this.depts = depts;
  //   console.log(depts);
  // });
  // }
};
</script>

<style>
/*  */
.avatar-uploader .el-upload{border:1px dashed #d9d9d9;border-radius:6px;cursor:pointer;position:relative;overflow:hidden;}
.avatar-uploader .el-upload:hover{border-color:#409eff;}
.avatar-uploader-icon{font-size:28px;color:#8c939d;width:100px;height:80px;line-height:80px;text-align:center;}
.avatar{width:100px;height:80px;display:block;}
/*  */
a{
  text-decoration: none;
  color: white;
}
/*  */
.image {
        width: 19px;
        height: 19px;
        display: inline-block;
        position: relative;
        top: 8px;
        vertical-align: 25%;
    }
/**/ 
.el-buttonOfHeader , .el-buttonOfHeader:hover, .el-buttonOfHeader:focus{
  background: #557479 ;
  color: white;
  font-weight: bold;
  border-color: #557479 ;
}
/* aside */
.aside{
 background-color:#3a4a6e;
}
/*  */
html,
body,
#app,
.homeBox{
  padding:0px;
  margin: 0px;
  height: 100%;
  overflow: auto;
} 
</style>