<template>
  <div class="homeBox">
    <el-container style="height: 100%;">
      <el-header style=" border-bottom: 1px solid #eee;display: flex; align-items: center; height: 50px;">
        <el-button class="el-buttonOfHeader" style="width: 205px;height: 50px;text-align:center;border-right: none;margin-left: -21px;margin-top: 1px;">
            <img class = "image" src="https://ts1.cn.mm.bing.net/th?id=OIP-C.l7DqF8uxZM2mgyGWBDQIHAHaHa&w=250&h=250&c=8&rs=1&qlt=90&o=6&dpr=1.3&pid=3.1&rm=2">
        </el-button>
        <div style="display:flex; align-items: center;">
          <!--  -->
          <el-breadcrumb separator-class="el-icon-arrow-right" style="margin-left: 20px;margin-top:10px;margin-bottom:10px;">
            <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
            <el-breadcrumb-item :to="{ path: '/dept' }"></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
          </el-breadcrumb>
         
        </div>
        <!-- <el-button class="el-buttonOfHeader" style="margin-left: auto; margin-right: 20px;"></el-button> -->
      </el-header>
      <el-container>
        <el-aside class="aside" width="204px">
            <el-menu 
                :default-active="$route.path" 
                class="el-menu-vertical-demo" 
                background-color="#3a4a6e "
                @select="handleSelect"
                text-color="#fff"
                active-text-color="#ffd04b"
                router
                style="border: 0px solid #3a4a6e">
                <el-menu-item index="/" >
                    <i class="el-icon-location"></i>
                    <span slot="title"></span>
                </el-menu-item>
                <el-submenu index="$route.path" >
                    <template slot="title">
                        <i class="el-icon-message"></i>
                        <span></span>
                    </template>
                    <el-menu-item index="/dept">
                    <i class="el-icon-edit"></i>
                    
                    </el-menu-item>
                    <el-menu-item index="/emp">
                    <i class="el-icon-edit"></i>
                    
                    </el-menu-item>
                </el-submenu>
            </el-menu>
        </el-aside>
        <el-main style="padding:20px">
          <!--  -->
          <el-button
            type="primary"
            size="mini"
            icon="el-icon-circle-plus"
            @click="addDialogVisible = true">
          </el-button>
          <!--  -->
          <el-table :data="tableData" border>
            <el-table-column prop="id" label="" width="120"></el-table-column>
            <el-table-column prop="name" label="" width="300"></el-table-column>
            <el-table-column prop="updateTime" label="" width="350">
               <template slot-scope="scope">
                 {{ new Date(scope.row.updateTime).toLocaleString().replace(/\//g,'-') }}
               </template>
            </el-table-column>
            <el-table-column label="">
              <template slot-scope="scope">
                <el-button
                  type="primary"
                  size="small"
                  @click="editDialogVisible = true; beforeHandleEdit(scope.row)"></el-button>
                <el-button
                  size="small"
                  type="danger"
                  @click="handleDelete(scope.row.id)"></el-button>
              </template>
            </el-table-column>
          </el-table>
          <!-- dialog -->
            <el-dialog title="" :visible.sync="addDialogVisible"
            :style="{ width: '55%', height: '98%', padding: '10px', margin: '6% auto'}" center>
            <el-form :model="addDialogForm" :rules="addDialogFormRules" ref="addDialogForm"
            label-position="right" label-width="80px">
              <el-form-item label="" prop="name">
                <el-input v-model="addDialogForm.name" autocomplete="off" placeholder=""></el-input>
              </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
              <el-button @click="addDialogVisible = false"> </el-button>
              <el-button type="primary"  @click="handleAdd"> </el-button>
            </div>
          </el-dialog>
          <!-- dialog -->
            <el-dialog title="" :visible.sync="editDialogVisible"
            :style="{ width: '55%', height: '98%', padding: '10px', margin: '6% auto'}" center>
            <el-form :model="editDialogForm" :rules="addDialogFormRules" ref="editDialogForm"
            label-position="right" label-width="80px">
              <el-form-item label=""  prop="name">
                <el-input v-model="editDialogForm.name" autocomplete="off" placeholder=""></el-input>
              </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
              <el-button @click="editDialogVisible = false"> </el-button>
              <el-button type="primary"  @click="handleEdit"> </el-button>
            </div>
          </el-dialog>
        </el-main>
      </el-container>
    </el-container>
  </div>
</template>

<script>
import axios from 'axios'
export default {
    data(){
        return{
            activeIndex : '/',
            tableData:[],
            addDialogVisible: false,
            editDialogVisible: false,
            addDialogFormRules: {
              name: [{ required: true, message: "", trigger: "blur" }],
            },
            addDialogForm:{
              name: "",
            },
            editDialogForm:{
              id:"",
              name: "",
            },

        }
    },
    watch: {
        $route () {
            this.handleSelect(this.activeIndex);
        }
     },
    methods: {
    //
    async handleAdd(){
      if(this.addDialogForm.name===""){
        return this.$message.error("");
      }
      const confirmR = await this.$confirm(
        "?",
        "",
        {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning",
          center: true,
        }
      ).catch((err) => err);
      if (confirmR == "confirm"){
        //
        axios.
          post("/api/depts/",{
              name: this.addDialogForm.name,
          },
          {
          headers:{
          'Content-Type': 'application/json',
          'token': localStorage.getItem('token')
          },
          })
          .then(result => {
          console.log(result.data.code);
          if(result.data.code == 0){
            return this.$message.error("~");
          }
          this.$message.success("");
          this.addDialogVisible = false;
          this.addDialogForm={//
                name: ""
          };
          this.fetchData();
        }).catch(error => {
          console.log(error);
          this.$message.error("");
        });
      }else{
        return this.$message.info("");
      }  
    },
    //
    async handleEdit(){
      if(this.editDialogForm.name===""){
        return this.$message.error("");
      }
      const confirmR = await this.$confirm(
        "?",
        "",
        {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning",
          center: true,
        }
      ).catch((err) => err);
      if (confirmR == "confirm"){
        //
        axios.
          put("/api/depts/",{
              name: this.editDialogForm.name,
              id:this.editDialogForm.id
          },
          {
          headers:{
          'Content-Type': 'application/json',
          'token': localStorage.getItem('token')
          },
        })
          .then(result => {
          console.log(result.data.code);
          if(result.data.code == 0){
            return this.$message.error("");
          }
          this.$message.success("");
          this.editDialogVisible = false;
          this.editDialogForm={//
                name: "",
          };
          this.fetchData();
        }).catch(error => {
          console.log(error);
          this.$message.error("");
        });
      }else{
        return this.$message.info("");
      }
    },
    //,
    beforeHandleEdit(rows){
     this.editDialogForm = JSON.parse(JSON.stringify(rows));
     this.editDialogForm.id = rows.id;
    },
    // 
    async handleDelete(id) {
      // console.log("id:", id);
      const confirmR = await this.$confirm(
        ", ?",
        "",
        {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning",
          center: true,
        }
      ).catch((err) => err);
      if (confirmR == "confirm") {
        // 
        axios.
          delete("/api/depts/"+id,{
            headers: {
            'token': localStorage.getItem('token')
          }
          })
          .then(result => {
          console.log(result.data.code);
          if(result.data.code == 0){
            return this.$message.error("");
          }
          this.$message.success("");
          this.fetchData();
        })
      }  else {
        return this.$message.info("");
      }
    },
      
        onSubmit() {
            console.log('submit!');
        },
        fetchData(){
          axios.get("api/depts",{
            headers:{
            'token': localStorage.getItem('token')
          },
          }).then((result) =>{
            this.tableData = result.data.data;
        });
        }

    },
    mounted() {
        //
        this.fetchData();
    },
    created(){
      // token
      if(!localStorage.getItem('token')){
        this.$router.push('/login');
        //router.push('/login');
      }
    },
};
</script >

<style>
</style>